import { Component, OnInit } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController } from 'ionic-angular';
import { CatalogueService } from '../../service/catalogue';
import { StatusBar } from '@ionic-native/status-bar';
import { Catalogue } from '../../models/catalogue';
/*
  Generated class for the Category page.

  See http://ionicframework.com/docs/v2/components/#navigation for more info on
  Ionic pages and navigation.
*/
@IonicPage()
@Component({
  selector: 'page-category',
  templateUrl: 'category.html',
  providers: [CatalogueService, StatusBar]
})
export class CategoryPage implements OnInit {

  title: string;
  cateId: number;
  pnum: number = 1;
  pqty: number = 10;
  catalogueList: Catalogue[];

  constructor(public navCtrl: NavController, public navParams: NavParams, private catalogueService: CatalogueService, public toaCtrl: ToastController, private statusBar: StatusBar) {
    this.catalogueList = [];
  }

  ngAfterViewInit() {

  }

  ngOnInit() {
    this.cateId = this.navParams.get('cateId');
    this.title = this.navParams.get('cateName');
    this.catalogueList = [];
    //this.statusBar.styleDefault();

    this.getItemList(this.cateId, this.pnum, this.pqty);

  }

  ionViewDidLoad() {

  }

  enterItem(item: any) {
    this.navCtrl.push('ItemPage', { item: item, saleRoomId: this.cateId });
  }

  getItemList(cateId: number, pnum: number, pqty: number) {
    this.catalogueService.getCatalogueList(cateId, pnum, pqty).subscribe(catalogueList => {
      this.pnum++;
      this.catalogueList = catalogueList;
    });
  }

  getMoreItem(infiniteScroll?) {//上拉加载
    this.catalogueService.getCatalogueList(this.cateId, this.pnum, this.pqty).subscribe(catalogueList => {
      if (catalogueList.length > 0) {
        this.pnum++;
        if (infiniteScroll) {
          infiniteScroll.complete();
        }
        this.catalogueList = this.catalogueList.concat(catalogueList);
      } else {
        if (infiniteScroll) {
          infiniteScroll.complete();
          infiniteScroll.enable(false);
        }
        this.toaCtrl.create({
          message: '已经到底了o(╯□╰)o',
          duration: 1000,
          //showCloseButton: true,
          dismissOnPageChange: true
        }).present();
      }
    }, error => {
      infiniteScroll.complete();
      this.toaCtrl.create({
        message: '好像有什么不对劲o(╯□╰)o',
        duration: 1000,
        //showCloseButton: true,
        dismissOnPageChange: true
      }).present();
    });
  }

}



