import { Injectable } from '@angular/core';
import { Http, Response, URLSearchParams, RequestOptions, Headers } from '@angular/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/switchMap';
import { Catalogue } from '../models/catalogue';
import { Setting } from '../app/app.setting';
import { WebSocketProvider } from '../providers/web-socket';
import { Device } from '@ionic-native/device';

@Injectable()
export class CatalogueService {
  private catalogueInfoUrl = this.setting.apiTestHost + '/artradeApp/catalogueinfo/getcatalogueinfobylotid.action';

  private catalogueListUrl = this.setting.apiTestHost + '/artradeApp/item/iteminput/querysriteminfo.action';

  private givePriceUrl = this.setting.apiTestHost + '/artradeApp/bid/addprice.action';

  private cataloguePriceUrl = this.setting.apiTestHost + '/artradeApp/common/itemlot/auctitem.action';

  constructor(private http: Http, private setting: Setting, private ws: WebSocketProvider, private device: Device) {

  }
  /** 监听出价信息 */
  listenBidings(logid: number): Observable<MessageEvent> {
    return this.ws.listen('/text/auct' + logid);
  }

  // 关闭ws连接
  disListenBidings() {
    this.ws.unsubscribe();
  }

  // 根据专场id获取拍品图录列表
  getCatalogueList(saleRoomId: number, currentPage: number, pageSize: number): Observable<Catalogue[]> {
    let params = new URLSearchParams();
    let queryData = {
      filterMap: {
        saleRoomId: saleRoomId + "",
        timeFlag: 'now' //｛"all":全部；"now"：未结束；"end":已结束｝
      },
      pageInfo: {
        currentPage: currentPage,
        pageCount: 0,
        recordCount: 0,
        pageSize: pageSize
      }
    };
    params.set('queryData', JSON.stringify(queryData));

    let options = new RequestOptions({
      headers: new Headers(),
      search: params
    });
    return this.http.get(this.catalogueListUrl, options)
      .map(res => {
        let result = res.json().data.data || [];
        result.forEach((item, index, array) => {
          item.ITEM_PIC_PATH = this.setting.apiTestHost + '/gallery' + item.ITEM_PIC_PATH + '_s.jpg';
        });
        return result;
      })
      .catch(this.handleError);
  }

  //根据拍品id获取拍品详情
  getCatalogueInfo(lotId: number, saleRoomId: number, memberId?: string): Observable<Catalogue> {

    let params = new URLSearchParams();
    let dataStr = {
      imei: this.device.uuid || '0',
      memberId: memberId || '',
      lotId: lotId,
      saleRoomId: saleRoomId,
      showRoom: ''
    };
    params.set('dataStr', JSON.stringify(dataStr));
    let options = new RequestOptions({
      headers: new Headers(),
      search: params
    });
    return this.http.get(this.catalogueInfoUrl, options)
      .map(res => {
        if (res.json().result) {
          let result = res.json().data;
          result.otherItemPicList = result.otherItemPicList || [];
          result.otherItemPicList.forEach((item, index, array) => {
            item.filepath = item.filepath + '.' + item.filetype;
          });
          return result;
        }
        return null;
      })
      .catch(this.handleError);
  }

  getNextPrice(lotId: number, inputValue: number, memberId?: string): Observable<number> {
    return this.getPrice(lotId, memberId, 1, inputValue);
  }

  getPrevPrice(lotId: number, inputValue: number, memberId?: string): Observable<number> {
    return this.getPrice(lotId, memberId, 0, inputValue);
  }

  giveNormalPrice(lotId: number, memberId: string, price: number): Observable<string> {
    return this.givePrice(lotId, memberId, price, 1);
  }

  giveProxyPrice(lotId: number, memberId: string, price: number): Observable<string> {
    return this.givePrice(lotId, memberId, price, 2);
  }

  private givePrice(lotId: number, memberId: string, price: number, type: number): Observable<string> {
    let params = new URLSearchParams();
    params.set('imei', this.device.uuid || '0');
    params.set('memberId', memberId || '');
    params.set('lotId', lotId + '');
    params.set('price', price + '');
    params.set('type', type + '');
    let options = new RequestOptions({
      headers: new Headers(),
      search: params
    });
    return this.http.get(this.givePriceUrl, options)
      .map(res => {
        return res.json().data;
      })
      .catch(this.handleError);
  }

  private getPrice(lotId: number, memberId: any, priceType: number, inputValue: number): Observable<number> {
    let params = new URLSearchParams();
    params.set('imei', this.device.uuid || '0');
    params.set('memberId', memberId || '');
    params.set('lotId', lotId + '');
    params.set('priceType', priceType + '');
    params.set('inputValue', inputValue + '');
    let options = new RequestOptions({
      headers: new Headers(),
      search: params
    });
    return this.http.get(this.cataloguePriceUrl, options)
      .map(res => {
        if (res.json().result) {
          let result = res.json().data;
          return result;
        }
        return null;
      })
      .catch(this.handleError);
  }

  private handleError(error: Response | any) {
    // In a real world app, we might use a remote logging infrastructure
    let errMsg: string;
    if (error instanceof Response) {
      const body = error.json() || '';
      const err = body.error || JSON.stringify(body);
      errMsg = `${error.status} - ${error.statusText || ''} ${err}`;
    } else {
      errMsg = error.message ? error.message : error.toString();
    }
    console.error(errMsg);
    return Observable.throw(errMsg);
  }
}



